* {
  box-sizing:border-box;
  font-family: sans-serif;
}

header {
  font-family: sans-serif;
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 32px;
  background: rgba(0,0,0,.5);
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
  font-size: 16px;
  color: #ddd;
}

header > div {
  margin: 0 8px;
}

header b {
  color: #fff;
  font-size: 24px;
}

h1 {
  font-size: 1.5rem;
  text-align: center;
  margin: 4px 0;
}

div.tbl {
  display: table;
  width: 100%;
  border-collapse: collapse;
}

div.tbl > div {
  display: table-row;
}

div.tbl > div.highlighted {
  box-shadow: 0 0 8px #f80;
  font-weight: bold;
  border-radius: 6px;
  overflow: hidden;
  background: rgba(255,140,0,.5);
}

div.tbl > div > div {
  display: table-cell;
  vertical-align: middle;
  padding: 3px 16px;
}

div.tbl > div > div:first-child {
  text-align: right;
}

div.tbl > div > div:last-child {
  text-align: left;
}

#congrat {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  position: absolute;
  left: 50%;
  top: 50%;
  width: 240px;
  height: 180px;
  margin: -90px -120px;
  border-radius: 8px;
  padding: 8px;
  background: rgba(32,16,0,.8);
  z-index: 9;
  text-align: center;
  font: 70%;
  opacity: 1;
  -webkit-transform: translateY(0);
          transform: translateY(0);
  -webkit-transition: all .3s;
  transition: all .3s;
}

#congrat.hide {
  opacity: 0;
  -webkit-transform: translateY(100vh);
          transform: translateY(100vh);
  -webkit-transition: all .3s;
  transition: all .3s;
}

#congrat input {
  border: none;
  text-align: center;
  font-size: 1.5rem;
}

#welcome {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  position: absolute;
  left: 50%;
  top: 50%;
  width: 240px;
  height: 320px;
  margin: -160px -120px;
  border-radius: 8px;
  padding: 8px;
  background: rgba(32,16,0,.8);
  z-index: 9;
  font: 70%;
  opacity: 1;
  -webkit-transform: translateY(0);
          transform: translateY(0);
  -webkit-transition: all .3s;
  transition: all .3s;
}

button {
  border: none;
  background: #F80;
  font-weight: bold;
  font-size: 1.5rem;
  padding: .5rem 2rem;
  border-radius: 8px;
  color: #ffd;
  cursor: pointer;
}

#welcome.hide {
  opacity: 0;
  -webkit-transform: translateY(-200%);
          transform: translateY(-200%);
  -webkit-transition: all .3s;
  transition: all .3s;
}

@media (max-width:480px) {
  header {
    font-size: 10px;
    height: 16px;
  }
  header b {
    color: #fff;
    font-size: 16px;
  }
}

html, body {
    position: absolute;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    width: 100%;
    height: 100%;
    overflow: hidden;
    margin: 0;
    padding: 0;
    color: #fff;
    background: #000;
}

canvas {
    position: absolute;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    width: 100%;
    height: 100%;
    margin: 0;
}

@-webkit-keyframes palpitation {
    from { opacity: .5; }
    to   { opacity: 1; }
}

@keyframes palpitation {
    from { opacity: .5; }
    to   { opacity: 1; }
}

div#PROGRESS {
    position: absolute;
    left: 0;
    top: 50vh;
    right: 0;
    width: 100%;
    height: 1px;
    z-index: 999999;
    background: #fff;
    font-size: 0;
    overflow: hidden;
    -webkit-transform: scaleX(0);
            transform: scaleX(0);
    -webkit-transition: -webkit-transform .4s;
    transition: -webkit-transform .4s;
    transition: transform .4s;
    transition: transform .4s, -webkit-transform .4s;
    -webkit-animation: palpitation 1.2s ease-in infinite alternate;
            animation: palpitation 1.2s ease-in infinite alternate;
}

div#SPLASH {
    display: block;
    position: fixed;
    margin: 0;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    width: 100%;
    height: 100%;
    background: #000;
    font-family: sans-serif;
    font-size: 10vw;
    text-shadow: none;
    color: #fff;
    z-index: 999999;
}

div#SPLASH.hide {
    -webkit-transition: opacity .5s;
    transition: opacity .5s;
    opacity: 0;
}


div#SPLASH > div:first-child {
    position: absolute;
    left: 0;
    right: 0;
    bottom: 50vh;
    text-align: center;
    opacity: 0;
    -webkit-transform: scale(.8);
            transform: scale(.8);
}

div#SPLASH > div:last-child {
    position: absolute;
    left: 0;
    right: 0;
    bottom: 50vh;
    text-align: center;
    opacity: 0;
    -webkit-transform: scale(.8,-.64);
            transform: scale(.8,-.64);
    -webkit-transform-origin: bottom;
            transform-origin: bottom;
    color: #222;
}

div#SPLASH.show > div:first-child {
    -webkit-transition: all 1s ease;
    transition: all 1s ease;
    opacity: 1;
    -webkit-transform: scale(1);
            transform: scale(1);
}

div#SPLASH.show > div:last-child {
    -webkit-transition: all 1s ease;
    transition: all 1s ease;
    opacity: 1;
    -webkit-transform: scale(1,-.8);
            transform: scale(1,-.8);
}

